    /**
     * VerifySlide
     * @description 滑块
     * */
    import {aesEncrypt} from "./../utils/ase.js"
	import {myRequest} from "../utils/request.js"
    export default {
        name: 'VerifySlide',
        props: {
            captchaType:{
                type:String,
            },
            type: {
                type: String,
                default: '1'
            },
            //弹出式pop，固定fixed
            mode: {
                type: String,
                default: 'fixed'
            },
            vSpace: {
                type: Number,
                default: 5
            },
            explain: {
                type: String,
                default: '向右滑动完成验证'
            },
            imgSize: {
                type: Object,
                default() {
                    return {
                        width: '310px',
                        height: '155px'
                    }
                }
            },
            blockSize: {
                type: Object,
                default() {
                    return {
                        width: '50px',
                        height: '50px'
                    }
                }
            },
            barSize: {
                type: Object,
                default() {
                    return {
                        width: '100%',
                        height: '40px'
                    }
                }
            },
			defaultImg: {
				type: String,
				default: ''
			}
        },
        data() {
            return {
                secretKey:'',        //后端返回的加密秘钥 字段
				passFalg:false,      //请求通过与否
                backImgBase:'',      //验证码背景图片
                blockBackImgBase:'', //验证滑块的背景图片
                backToken:"",        //后端返回的唯一token值
                startMoveTime:"",    //移动开始的时间
                endMovetime:'',      //移动结束的时间
                tipsBackColor:'',    //提示词的北京颜色
                tipWords:'',
                text: '',
                finishText:'',
                setSize: {
                    imgHeight: 0,
                    imgWidth: 0,
                    barHeight: 0,
                    barWidth: 0
                },
                top: 0,
                left: 0,
                moveBlockLeft: undefined,
                leftBarWidth: undefined,
                // 移动中样式
                moveBlockBackgroundColor: undefined,
                leftBarBorderColor: '#ddd',
                iconColor: undefined,
                iconClass: 'icon-right',
                status: false,	    //鼠标状态
                isEnd: false,		//是够验证完成
                showRefresh: true,
                transitionLeft: '',
                transitionWidth: ''
            }
        },
        methods: {
            init() {
                this.text = this.explain
                this.getPictrue();
                this.$nextTick(() => {
                    this.$parent.$emit('ready', this)
                })
            },

            //鼠标按下
            start: function (e) {
                this.startMoveTime = new Date().getTime();   //开始滑动的时间
                if (this.isEnd == false) {
                    this.text = ''
                    this.moveBlockBackgroundColor = '#337ab7'
                    this.leftBarBorderColor = '#337AB7'
                    this.iconColor = '#fff'
                    e.stopPropagation();
                    this.status = true;
                }
            },
            //鼠标移动
            move: function (e) {
                var query = uni.createSelectorQuery().in(this);
                this.barArea = query.select('.verify-bar-area')
                var bar_area_left,barArea_offsetWidth;
                this.barArea.boundingClientRect(data => {
                    bar_area_left = Math.ceil(data.left) 
                    barArea_offsetWidth =Math.ceil(data.width) 

                if (this.status && this.isEnd == false) {
                    if (!e.touches) {   //兼容移动端
                        var x =Math.ceil(e.clientX);
                    } else {     //兼容PC端
                        var x =Math.ceil(e.touches[0].pageX) ;
                    }
                    // var bar_area_left = this.getLeft(this.barArea);
                    
                    var move_block_left = x - bar_area_left //小方块相对于父元素的left值
                    if (this.type !== '1') {		//图片滑动
                        if (move_block_left >= barArea_offsetWidth - parseInt(parseInt(this.blockSize.width) / 2) - 2) {
                            move_block_left = barArea_offsetWidth - parseInt(parseInt(this.blockSize.width) / 2) - 2;
                        }
                    } 

                    if (move_block_left <= 0) {
                        move_block_left = parseInt(parseInt(this.blockSize.width) / 2);
                    }

                    //拖动后小方块的left值
                    this.moveBlockLeft = (move_block_left - parseInt(parseInt(this.blockSize.width) / 2) ) + "px"
                    this.leftBarWidth = (move_block_left - parseInt(parseInt(this.blockSize.width) / 2)) + "px"
                    
                }
                }).exec();
            },

            //鼠标松开
            end: function () {
                this.endMovetime =new Date().getTime(); 
                var _this = this;
//                判断是否重合
                if (this.status && this.isEnd == false) {
                    if (this.type !== '1') {		//图片滑动
                        var moveLeftDistance = parseInt((this.moveBlockLeft || '').replace('px', ''));
                        
                        moveLeftDistance = moveLeftDistance * 310/ parseInt(this.imgSize.width)

                        var captchaVerification = this.secretKey ?aesEncrypt(this.backToken+'---'+JSON.stringify({x:moveLeftDistance,y:5.0}),this.secretKey):this.backToken+'---'+JSON.stringify({x:moveLeftDistance,y:5.0})
                        let data = {
                            captchaType:this.captchaType,
                            "pointJson":this.secretKey ? aesEncrypt(JSON.stringify({x:moveLeftDistance,y:5.0}),this.secretKey):JSON.stringify({x:moveLeftDistance,y:5.0}),
                            "token":this.backToken
                        }
                        myRequest({
                            url: `/captcha/check`,
                            data,
                            method:"POST",
                        }).then((result) => {
							let res = result.data
							if (res.repCode == "0000") {
								this.moveBlockBackgroundColor = '#5cb85c'
								this.leftBarBorderColor = '#5cb85c'
								this.iconColor = '#fff'
								this.iconClass = 'icon-check'
								this.showRefresh = true
								this.isEnd = true;   
								setTimeout(()=>{
									if (this.mode=='pop') {
										this.$parent.clickShow = false;
									}
									this.refresh();
								},1500)
								this.passFalg = true
								this.tipWords = `${((this.endMovetime-this.startMoveTime)/1000).toFixed(2)}s验证成功`
								setTimeout(()=>{
									this.tipWords = ""
									this.$emit('success', {captchaVerification})
									// this.$parent.$emit('success', {captchaVerification})
								},1000)
							}else{
								this.moveBlockBackgroundColor = '#d9534f'
								this.leftBarBorderColor = '#d9534f'
								this.iconColor = '#fff'
								this.iconClass = 'icon-close'
								this.passFalg = false
								setTimeout(()=>{
									this.refresh();
								}, 1000);
								this.$parent.$emit('error',this)
								this.tipWords = "验证失败"
								setTimeout(()=>{
									this.tipWords = ""
								},1000)
							}
						})
                    } 
                    this.status = false;
                }
            },
            refresh: function () {
                this.showRefresh = true
                this.finishText = ''
                this.transitionLeft = 'left .3s'
                this.moveBlockLeft = 0
                this.leftBarWidth = false
                this.transitionWidth = 'width .3s'
                this.leftBarBorderColor = '#ddd'
                this.moveBlockBackgroundColor = '#fff'
                this.iconColor = '#000'
                this.iconClass = 'icon-right'
                this.getPictrue()
                this.isEnd = false
                setTimeout(() => {
                    this.transitionWidth = ''
                    this.transitionLeft = ''
                    this.text = this.explain
                }, 300)
            },

            //获取left值
            getLeft: function (node) {
                let leftValue = 0;
                while (node) {
                    leftValue += node.offsetLeft;
                    node = node.offsetParent;
                }
                let finalvalue = leftValue;
                return finalvalue;
            },

            // 请求背景图片和验证图片
            getPictrue(){
                let data = {
                    captchaType:this.captchaType,
					clientUid: uni.getStorageSync('slider'), 
					ts: Date.now(), // 现在的时间戳
                }
                myRequest({
                    url: '/captcha/get', //仅为示例，并非真实接口地址。
                    data,
                    method:"POST",
                }).then((result) => {
                        let res = result.data
                        if (res.repCode == "0000") {
                            this.backImgBase = res.repData.originalImageBase64
                            this.blockBackImgBase = res.repData.jigsawImageBase64
                            this.backToken = res.repData.token
                            this.secretKey = res.repData.secretKey
                        }
						// 判断接口请求次数是否失效
						if(res.repCode == '6201') {
							this.backImgBase = null
							this.blockBackImgBase = null
						}
                    })
            },
        },
        watch: {
            // type变化则全面刷新
            type: {
                immediate: true,
                handler() {
                    this.init()
                }
            }
        },
        mounted() {
        },
    }
